<template>
  <div id="header">
    <nav class="navbar navbar-expand fixed-top wz-top-header p-0">
      <div class="container container-fluid">
        <div class="wz-navbar-header">
          <span class="wz-navbar-header__logo" :class="{'mobile_page_name': $DEVICE <= 3}" @click="$scroll.to('#header')">
            <template>
              <!--<div class="heartbeat">-->
                <!--<i class="mdi mdi-heart"></i>-->
              <!--</div>-->
              <img src="../../static/img/logo.jpg" alt="logo">
                CORDIS
              <span v-if="$DEVICE > 3" class="slogan">
                - на страже вашего здоровья 24/7. Гарантия анонимности
              </span>
            </template>
          </span>
          <!--<div class="for-button" v-if="$DEVICE === 5">-->
            <!--<b-button size="md" variant="primary" @click="$scroll.to('#onas')">-->
              <!--О нас-->
            <!--</b-button>-->
            <!--<b-button size="md" variant="primary" @click="$scroll.to('#contacts')">-->
              <!--Контакты-->
            <!--</b-button>-->
          <!--</div>-->
        </div>
        <div style="cursor: pointer" v-call class="wz-right-navbar" v-if="$DEVICE > 3">
          +380 (67) 103-8-103
        </div>
        <div v-else class="header-menu" @click="$store.commit('TOGGLE_MODAL_MENU')">
          <i class="fas fa-bars"></i>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  @import "../../static/scss/variables";

  .navbar-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    li {
      padding: 0 10px;
      cursor: pointer;
      i {
        font-size: 1.5rem;
        &:hover {
          color: #dc3545
        }
      }
    }
  }

  .nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    list-style: none;
  }

  .wz-top-header {
    background-image: linear-gradient(291deg, $secondary_color 33%, $accent_color 11%);
    /*background-image: linear-gradient(291deg, $secondary_color 33%, $primary_color 11%, $accent_color);*/
    z-index: 3 !important;
    background-color: #FFF;
    border: 0;
    margin-bottom: 0;
    padding: 0;
    height: $header_height;
    .wz-navbar-header {
      color: white;
      display: flex;
      width: 100%;
      .wz-navbar-header__logo {
        line-height: 60px;
        height: 60px;
        font-size: 32px;
        font-weight: bold;
        padding: 0 20px;
        margin: 0;
        display: block;
      }
      .wz-navbar-header__search {
        display: inline-block;
        margin: auto;
        width: 100%;
      }
      .for-button {
        align-items: center;
        display: flex;
        button {
          margin-left: 10px !important;
        }
      }
    }
    .page-title {
      font-size: 1.462rem;
      line-height: 60px;
      float: left;
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
      padding: 0 20px;
      .icon {
        font-size: 1.7rem;
        margin-right: 10px;
      }
    }
    .wz-right-navbar {
      min-width: 270px;
      font-size: 28px;
      font-weight: bold;
    }
    &.searching {
      .wz-navbar-header {
        width: 100%;
      }
      .wz-right-navbar {
        width: 0;
      }
    }
    .header-menu {
      position: absolute;
      right: 0;
      padding: 20px;
      color: white;
      font-size: 1.4em;
    }
  }

  @media (max-width: 1199px) {
    .wz-top-header .wz-navbar-header .wz-navbar-header__logo {
      font-size: 26px;
    }
  }

  @media (max-width: 992px) {
    .wz-top-header {
      .header-menu {
        color: #212529;
      }
    }
  }

  @media (max-width: 768px) {
    .wz-top-header {
      .header-menu {
        color: white;
      }
    }
    .wz-top-header {
      height: 50px;
      background: $accent_color;
    }
    .wz-top-header {
      .wz-navbar-header {
        min-width: 100%;
        .wz-navbar-header__logo {
          width: 100%;
          height: 50px;
          padding: 0 10px;
          line-height: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
          h1 {
            display: flex;
            align-items: center;
            margin-bottom: 0;
            justify-content: center;
            font-size: 1.2rem;
            text-align: center;
            small {
              color: #dc3545;
              display: block;
            }
            span {
              flex: none;
            }
            i {
              margin-right: 10px !important;
              flex: none;
            }
          }
          i {
            flex: 1;
            margin-right: 10px;
          }
          span {
            flex: 5;
            text-align: center;
            font-size: 1.2rem;
            i {
              margin-right: 3px;
              font-size: 24px;
            }
          }
        }
      }
    }
  }

  .wz-navbar-header__logo {
    &:hover {
      .heartbeat {
        animation: heartbeatt 1s infinite;
      }
    }
  }
  .heartbeat {
    display: inline-block;
    /*color: #f9a433;*/
    color: #ffc107;
  }

  .slogan {
    display: inline-block;
    font-size: 0.6em;
    position: relative;
    bottom: 4px;
  }

  @keyframes heartbeatt
  {
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
  }
</style>



