<template>
  <footer>
    <b-container>
      <div class="footer-flex">
        <div>
          <div class="heartbeat">
            <i class="mdi mdi-heart"></i>
          </div>
          CORDIS
          <span class="slogan">
            <template v-if="$DEVICE > 2">-</template> на страже вашего здоровья 24/7.<br v-if="$DEVICE === 1"> Гарантия анонимности
                  </span>
        </div>
        <div class="copyright">
          2017 - {{ new Date().getFullYear() }} ® Cordis.com.ua
        </div>
      </div>
    </b-container>
  </footer>
</template>

<script>
  export default {
    data: () => ({})
  }
</script>

<style lang="scss">
  footer {
    height: 70px;
    background: #212529;
    color: #D8D8D7;
    font-size: 2em;
    display: flex !important;
    align-items: center;
    .footer-flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .copyright {
        font-size: 0.5em;
      }
    }
  }

  @media (max-width: 992px) {
    footer {
      height: inherit;
      text-align: center;
      .footer-flex {
        display: block;
      }
      div {
        padding: 5px 0;
      }
    }
  }
</style>



